@use "../util" as *;

.card-overview {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  row-gap: rem(23);

  img,
  .card__change {
    justify-self: end;
  }

  .card__count {
    margin-bottom: 0;
  }

  .card__count,
  .card__change {
    align-self: end;
  }
}
